
.container {
  min-height: 100vh;
  background: linear-gradient(135deg, #f6f9fc 0%, #edf2ff 100%);
  padding: 2rem 1rem;
}

.searchCard {
  background: white;
  border-radius: 1.5rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  padding: 2.5rem;
  margin: 2rem auto;
  max-width: 1200px;
}

.title {
  color: #2563eb;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
  letter-spacing: -0.02em;
}

.searchForm {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.select, .input {
  width: 100%;
  padding: 1rem 1.25rem;
  border: 2px solid #e2e8f0;
  border-radius: 1rem;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #f8fafc;
  color: #334155;
}

.select:focus, .input:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
  background: white;
}

.button {
  grid-column: 1 / -1;
  background: #2563eb;
  color: white;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  letter-spacing: 0.02em;
}

.button:hover {
  background: #1d4ed8;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.2);
}

.button:disabled {
  background: #94a3b8;
  cursor: not-allowed;
  transform: none;
}

.tableWrapper {
  overflow-x: auto;
  margin-top: 2rem;
  border-radius: 1rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
}

.table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  background: white;
  border-radius: 1rem;
}

.tableHeader {
  background: #eff6ff;
  color: #1e40af;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.875rem;
  letter-spacing: 0.05em;
}

.tableHeader th {
  padding: 1.25rem 1rem;
  text-align: left;
  border-bottom: 2px solid #e2e8f0;
}

.tableCell {
  padding: 1rem;
  border-bottom: 1px solid #f1f5f9;
  color: #334155;
}

.trainRow {
  cursor: pointer;
  transition: all 0.2s ease;
}

.trainRow:hover {
  background: #f8fafc;
  transform: translateY(-1px);
}

.trainNumber {
  color: #2563eb;
  font-weight: 600;
}

.noTrains {
  text-align: center;
  color: #64748b;
  padding: 3rem 1rem;
  font-size: 1.1rem;
}

@media (max-width: 768px) {
  .searchCard {
    padding: 1.5rem;
  }
  
  .title {
    font-size: 2rem;
  }
  
  .tableWrapper {
    margin: 1rem -1rem;
    border-radius: 0;
  }
}
